import React, { PureComponent } from "react";
import { View, Text, ScrollView, PanResponder, Animated } from "react-native";

export const ProcessEditor = (props) => {
    const [position, setState] = React.useState({ x: 0, y: 0 });
    const lastPosition = React.useRef({ x:0, y:0 }).current;
    const panResponder = React.useMemo(() => PanResponder.create({
        onStartShouldSetPanResponder: (evt, gestureState) => true,
        onPanResponderMove: (evt, gestureState) => {    
            setState({ x: lastPosition.x + gestureState.dx, y: lastPosition.y +  gestureState.dy });
        },
        onPanResponderRelease: (evt, gestureState) => {            
            lastPosition.x += gestureState.dx;
            lastPosition.y += gestureState.dy;
            console.log(lastPosition);
        },
    }), []);

    return (
        <View style={{ flex: 1, flexDirection: "row", overflow: "hidden" }} {...panResponder.panHandlers}>
        <View style={{ flex: 5, zIndex: 12, transform: ([ { translateX: position.x }, { translateY: position.y }]), overflow: "hidden" }}>
            <Text style={{ position: "absolute", top: 0, left: 0 }}>A</Text>
            <Text style={{ position: "absolute", top: 100, left: 100 }}>A</Text>
            <Text style={{ position: "absolute", top: 200, left: 200 }}>A</Text>
            <Text style={{ position: "absolute", top: 300, left: 300 }}>A</Text>
            <Text style={{ position: "absolute", top: 400, left: 400 }}>A</Text>
            <Text style={{ position: "absolute", top: 500, left: 500 }}>A</Text>
            <Text style={{ position: "absolute", top: 600, left: 600 }}>A</Text>
        </View>   
        <ScrollView style={{ flex: 1, zIndex: 13, backgroundColor: 'rgba(255,255,255,1)', transform: ([{ translateX: 0 }, { translateY: 0 }]) }} scrollEnabled={ false }>
            <Text style={{ position: "absolute", top: 0, left: 0 }}>A</Text>
            <Text style={{ position: "absolute", top: 100, left: 100 }}>A</Text>
            <Text style={{ position: "absolute", top: 200, left: 200 }}>A</Text>
            <Text style={{ position: "absolute", top: 300, left: 300 }}>A</Text>
            <Text style={{ position: "absolute", top: 400, left: 400 }}>A</Text>
            <Text style={{ position: "absolute", top: 500, left: 500 }}>A</Text>
            <Text style={{ position: "absolute", top: 600, left: 600 }}>A</Text>
        </ScrollView>
        </View>
    );
}